/* Typography*/
@import url("https://fonts.googleapis.com/css?family=Karla:400|Spectral:400");
/* alerts  onloading animations */
@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}
.loading {
  width: 6rem;
  height: 6rem;
  margin: 0 auto;
  border: 5px solid #94a3b8;
  border-radius: 50%;
  border-top-color: #645cff;
  animation: spinner 0.6s linear infinite;
}

html {
  font-size: 100%;
}

/*16px*/
body {
  display: grid;
  place-items: center;
  background: white;
  font-family: "Karla", sans-serif;
  font-weight: 400;
  line-height: 1.75;
  color: #0f172a;
}

p {
  max-width: 40em;
  margin-bottom: 1.5rem;
}

h1, h2, h3, h4, h5 {
  margin: 0;
  margin-bottom: 1.38rem;
  font-family: "Spectral", serif;
  font-weight: 400;
  line-height: 1.3;
  text-transform: capitalize;
}

h1 {
  margin-top: 0;
  font-size: 3.052rem;
}

h2 {
  font-size: 2.441rem;
}

h3 {
  font-size: 1.953rem;
}

h4 {
  font-size: 1.563rem;
}

h5 {
  font-size: 1.25rem;
}

small,
.text-small {
  font-size: 0.7em;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
  padding: 0;
}

/* buttons */
.btn {
  cursor: pointer;
  color: white;
  background: #645cff;
  border: transparent;
  border-radius: 0.25rem;
  letter-spacing: 1px;
  padding: 0.75rem 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  transition: 0.3s ease-in-out all;
  text-transform: capitalize;
  text-align: center;
  display: inline-block;
  /*block class is good with forms*/
}
.btn:hover {
  background: #3c3799;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}
.btn-hipster {
  color: #645cff;
  background: #c0bdff;
}
.btn-hipster:hover {
  color: #c0bdff;
  background: #3c3799;
}
.btn-block {
  width: 100%;
}

/* alerts */
.alert {
  padding: 0.375rem 0.75rem;
  margin-bottom: 1rem;
  border-color: transparent;
  border-radius: 0.25rem;
}

.alert-errors {
  color: #842029;
  background: #f8d7da;
}

.alert-success {
  color: #0f5132;
  background: #d1e7dd;
}

/* forms */
.form {
  width: 100%;
  max-width: 470px;
  background: white;
  border-radius: 1.5rem;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  padding: 2rem;
  margin: 3rem auto;
}

.form-label {
  display: block;
  margin-left: 1.5rem;
  font-size: 0.7em;
  text-transform: capitalize;
  letter-spacing: 1px;
}

.form-input,
.form-textarea {
  width: 100%;
  padding: 0.75rem 1.5rem;
  border-radius: 0.25rem;
  background: #f8fafc;
  border: 1px solid #e2e8f0;
}

.form-group {
  width: 100%;
  padding-bottom: 1rem;
}

.form-textarea {
  height: 7rem;
}

::placeholder {
  font-family: inherit;
  color: #94a3b8;
}

.form-alert {
  color: #842029;
  letter-spacing: 1px;
  text-transform: capitalize;
  margin-left: 1.5rem;
}

.signUp {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
}
.signUp .form-group {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 0.25rem;
}
.signUp .btnFooter {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
.signUp .btnFooter .smallText {
  width: 100%;
  max-width: 470px;
}

.signIn {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 0.5rem;
}
.signIn strong {
  width: 100%;
  display: block;
}
.signIn__links {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.625rem;
}
.signIn__links span {
  width: 73px;
  height: 60px;
  background-color: salmon;
  display: grid;
  place-items: center;
}

/* titles */
.title {
  text-align: center;
}

.title-underline {
  background: #645cff;
  width: 7rem;
  height: 0.25rem;
  margin: 0 auto;
  margin-top: -1rem;
}

/* images */
.imgContainer {
  width: 40vw;
  max-width: 1120px;
  border-radius: 0.25rem;
}
.imgContainer .img {
  width: 100%;
  display: block;
  object-fit: cover;
}

*,
::after,
::before {
  box-sizing: border-box;
}

.container {
  width: 90%;
  max-width: 1120px;
}